body {
  --color-black: #000;
  --color-blue: #00acdc;
  margin: 0;
  font-family: Roboto, sans-serif;
}

ul, li {
  margin: 0;
  padding: 0;
}

.container {
  margin: 0 auto;
  padding-inline: 50px;
}

.header__wrap {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.nav__logo {
  display: block;
}

.nav ul {
  list-style-type: none;
  display: flex;
}

.nav li:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  color: var(--color-black);
  text-transform: uppercase;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.nav__link:hover, .is-active {
  color: var(--color-blue);
}

.is-active:after {
  content: "";
  background-color: var(--color-blue);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

